@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 400;
  src: url("styles/fonts/Raleway-Black.ttf") format("woff2"),
    url("styles/fonts/Raleway-Black.ttf") format("woff"),
    url("styles/fonts/Raleway-Black.ttf") format("truetype");
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 200;
  src: url("styles/fonts/Raleway-SemiBold.ttf") format("woff2"),
    url("styles/fonts/Raleway-SemiBold.ttf") format("woff"),
    url("styles/fonts/Raleway-SemiBold.ttf") format("truetype");
}
@font-face {
  font-family: "Hans";
  font-style: normal;
  src: url("styles/fonts/NotoSansKR-Black.otf") format("woff2"),
    url("styles/fonts/NotoSansKR-Black.otf") format("woff"),
    url("styles/fonts/NotoSansKR-Black.otf") format("truetype");
}
body {
  background: #d8e6e7 !important;
}
a,
a:link,
a:visited,
a:active {
  color: inherit;
  letter-spacing: normal;
  text-decoration: none;
  letter-spacing: -0.8px;
}
input {
  outline: none;
}
a:hover {
  text-decoration: none;
  color: inherit;
}
html {
  background-color: #9dc8c8;
}
